import { MainService } from './main.service';
import { Observable } from 'rxjs/Observable';
import { Injectable } from '@angular/core';
import { CanActivate, Route, Router, ActivatedRouteSnapshot, RouterStateSnapshot, CanLoad } from '@angular/router';

@Injectable()
export class AuthGuardService implements CanLoad, CanActivate {

  constructor(private mainServ: MainService, private router: Router) {
  }
  canLoad(): Observable<boolean> | Promise<boolean> | boolean {
    if (this.mainServ.authServ.isLogin()) {
      return true;
    }

    this.mainServ.globalServ.goTo("/login");
    return false;

  }

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean> | Promise<boolean> | boolean {

    let path = route.url[0].path
    console.log("path")
    console.log(path)
    if (this.mainServ.authServ.isLogin()) {
      if (this.mainServ.globalServ.isAllowedPage(path) == false) {
        this.router.navigate(["/list-royalty"])
        return false;
      }
      else
        return true;
    } else {
      this.mainServ.globalServ.goTo("/login");
      return false;
    }

  }
}



